<template>
    <div id="special">
        <div>
            <img src="../assets/images/service.jpg" alt="">
        </div>
        <div class="special-main-top">
            <p class="special-main-top-title"></p>
            <div class="special-main-top-list">
                <div class="special-main-top-item">
                    <div class="special-main-top-box special-main-top-box-1">
                        <p class="special-main-top-box-title"></p>
                        <div class="special-main-top-box-icon special-main-top-box-icon-1"></div>
                        <p class="special-main-top-box-info"></p>
                    </div>
                </div>
                <div class="special-main-top-item">
                    <div class="special-main-top-box special-main-top-box-2">
                        <p class="special-main-top-box-title"></p>
                        <div class="special-main-top-box-icon special-main-top-box-icon-2"></div>
                        <p class="special-main-top-box-info"></p>
                    </div>
                </div>
                <div class="special-main-top-item">
                    <div class="special-main-top-box special-main-top-box-3">
                        <p class="special-main-top-box-title"></p>
                        <div class="special-main-top-box-icon special-main-top-box-icon-3"></div>
                        <p class="special-main-top-box-info"></p>
                    </div>
                </div>
            </div>
        </div>
        <hr>
        <div class="special-main-center">
            <p class="special-main-center-title"></p>
            <ul class="special-main-center-ul">
                <li :class="[showBg == 1?'clicked':'def']" @click="showChart(1)"></li>
                <li :class="[showBg == 2?'clicked':'def']" @click="showChart(2)"></li>
                <li :class="[showBg == 3?'clicked':'def']" @click="showChart(3)"></li>
                <li :class="[showBg == 4?'clicked':'def']" @click="showChart(4)"></li>
                <li :class="[showBg == 5?'clicked':'def']" @click="showChart(5)"></li>
            </ul>
        </div>
        <div class="special-main-other">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
        </div>
        <div class="kong"></div>
        <div class="special-main-bottom">
            <ul class="special-main-bottom-ul">
                <li>
                    <div class="special-main-bottom-img">
                        <img src="../assets/images/dot.png" alt="5">
                    </div>
                    <p class="special-main-bottom-title">5</p>
                    <p class="special-main-bottom-num">50000</p>
                </li>
                <li>
                    <div class="special-main-bottom-img">
                        <img src="../assets/images/worker.png" alt="20">
                    </div>
                    <p class="special-main-bottom-title">20</p>
                    <p class="special-main-bottom-num">200000</p>
                </li>
                <li>
                    <div class="special-main-bottom-img">
                        <img src="../assets/images/area.png" alt="90%">
                    </div>
                    <p class="special-main-bottom-title">90%</p>
                    <p class="special-main-bottom-num">90%</p>
                </li>
                <li>
                    <div class="special-main-bottom-img">
                        <img src="../assets/images/coverage.png" alt="99%">
                    </div>
                    <p class="special-main-bottom-title">99%</p>
                    <p class="special-main-bottom-num">99%</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    metaInfo: {
        meta: [
            {
                name: 'keywords',
                content: ',,'
            },
            {
                name: 'description',
                content: ''
            }
        ]
    },
    data() {
        return {
            showBg: 0,
            specialData: []
        }
    },
    methods: {
        showChart(val) {
            this.showBg = val;
        },
        getData() {
            this.$axios.get('/api/special').then(res=>{
                this.specialData = res.data.data
            })
            .catch(res=>{
                alert('')
            })
        }
    },
    created() {
        this.getData();
    }
}
</script>

<style scoped>
    #special img {
        width: 100%;
    }
    .special-main-top {
        width: 1100px;
        margin: 0 auto;
        text-align: center;
        padding: 50px 0 70px;
    }
    .special-main-top-title {
        font-size: 1.4rem;
        font-weight: 700;
        letter-spacing:4px;
        padding-bottom: 50px;
    }
    .special-main-top-list::after {
        content: '';
        clear: both;
        display: block;
        visibility: hidden;
        height: 0;
    }
    .special-main-top-item {
        width: 33.3%;
        box-sizing: border-box;
        padding: 0 50px;
        float: left;
    }
    .special-main-top-box {
        width: 100%;
        border: 1px solid #666;
        border-radius: 3px;
        padding: 30px 0 60px;
        box-sizing: border-box;
    }
    .special-main-top-box-title {
        font-weight: 700;
        font-size: 0.8rem;
    }
    .special-main-top-box-icon {
        width: 80px;
        height: 80px;
        margin: 50px auto;
    }
    .special-main-top-box-icon-1 {
        background: url('../assets/images/user_test_1.png') center/cover no-repeat;
    }
    .special-main-top-box-icon-2 {
        background: url('../assets/images/open_price_1.png') center/cover no-repeat;
    }
    .special-main-top-box-icon-3 {
        background: url('../assets/images/professional_ethics_1.png') center/cover no-repeat;
    }
    .special-main-top-box-info {
        font-size: 0.4rem;
    }
    .special-main-top-box:hover {
        color: #fff;
        background: #1DA47B;
        border: 1px solid #1DA47B;
    }
    .special-main-top-box-1:hover .special-main-top-box-icon-1{
        background: url('../assets/images/user_test_2.png') center/cover no-repeat;
    }
    .special-main-top-box-2:hover .special-main-top-box-icon-2{
        background: url('../assets/images/open_price_2.png') center/cover no-repeat;
    }
    .special-main-top-box-3:hover .special-main-top-box-icon-3{
        background: url('../assets/images/professional_ethics_2.png') center/cover no-repeat;
    }

/*****  *****/
    .special-main-center {
        width: 1100px;
        margin: 0 auto;
        text-align: center;
        padding: 60px 0 20px;
    }
    .special-main-center-title {
        font-size: 1.4rem;
        font-weight: 700;
        letter-spacing:4px;
        padding-bottom: 50px;
    }
    .special-main-center-ul::after {
        content: '';
        clear: both;
        display: block;
        visibility: hidden;
        height: 0;
    }
    .special-main-center-ul li {
        width: 120px;
        height: 120px;
        float: left;
        border-radius: 50%;
        line-height: 120px;
        font-size: 0.4rem;
        margin-right: 120px;
        cursor: pointer;
    }
    .special-main-center-ul li:last-child {
        margin-right: 0;
    }
    .def {
        background: #F2F2F2;
    }
    .clicked {
        background: #1DA47B;
        color: #fff;
    }
    .special-main-other {
        background: #F2F2F2;
        padding: 80px 0 160px;
        text-align: center;
        line-height: 4;
    }

    .special-main-other p {
        font-size: 0.4rem;
    }
    .kong {
        width: 100%;
        height: 100px;
    }

/*****  *****/
    .special-main-bottom {
        background: #1DA47B;
    }
    .special-main-bottom-ul {
        width: 1100px;
        margin: 0 auto;
        text-align: center;
        padding: 60px 0 80px;
        color: #fff;
        display:-webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content:space-between;
        justify-content:space-between;
        -moz-box-pack:space-between;
        -webkit--moz-box-pack:space-between;
        box-pack:space-between;
    }
    .special-main-bottom-img {
        width: 80px;
        height: 80px;
        margin: 20px auto;
    }
    .special-main-bottom-title {
        font-size: 0.8rem;
        margin-bottom: 60px;
    }
    .special-main-bottom-num {
        font-weight: 700;
        font-size: 2rem;
    }
</style>



