<template>
    <div id="introduce">
        <div>
            <img src="../assets/images/about.jpg" alt="">
        </div>
        <div class="introduce-main">
            <p class="introduce-main-title">{{introduceData.title}}</p>
            <p class="introduce-main-text">{{introduceData.text1}}</p>
            <p class="introduce-main-text">{{introduceData.text2}}</p>
            <ul class="introduce-main-ul">
                <li>
                    <div class="introduce-main-img">
                        <img src="../assets/images/location.png" alt="">
                    </div>
                    <p class="introduce-main-info"></p>
                    <p class="introduce-main-msg"></p>
                </li>
                <li>
                    <div class="introduce-main-img">
                        <img src="../assets/images/mission.png" alt="">
                    </div>
                    <p class="introduce-main-info"></p>
                    <p class="introduce-main-msg"></p>
                    <p class="introduce-main-msg"></p>
                </li>
                <li>
                    <div class="introduce-main-img">
                        <img src="../assets/images/worth.png" alt="">
                    </div>
                    <p class="introduce-main-info"></p>
                    <p class="introduce-main-msg"> </p>
                    <p class="introduce-main-msg"> </p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    metaInfo: {
        meta: [
            {
                name: 'keywords',
                content: ',,'
            },
            {
                name: 'description',
                content: ''
            }
        ]
    },
    data() {
        return {
            introduceData: []
        }
    },
    methods: {
        getData() {
            this.$axios.get('/api/introduce').then(res=>{
                this.introduceData = res.data.data
            })
            .catch(res=>{
                alert('')
            })
        }
    },
    created() {
        this.getData();
    }
}
</script>

<style scoped>
    #introduce img {
        width: 100%;
    }
    .introduce-main {
        width: 1100px;
        margin: 0 auto;
        padding: 60px 0 200px;
    }
    .introduce-main-title {
        margin-bottom: 20px;
    }
    .introduce-main-text {
        font-size: 0.6rem;
        line-height: 1.6;
    }
    .introduce-main-ul {
        margin-top: 120px;
        text-align: center;
        display:-webkit-box;
        display: -moz-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content:space-between;
        justify-content:space-between;
        -moz-box-pack:space-between;
        -webkit--moz-box-pack:space-between;
        box-pack:space-between;
    }
    .introduce-main-ul li {
        padding: 0 50px;
    }
    .introduce-main-img {
        width: 100px;
        height: 100px;
        margin: 0 auto;
    }
    .introduce-main-info {
        margin: 30px 0 20px;
    }
    .introduce-main-msg {
        font-size: 0.6rem;
    }
</style>



